import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';
import {IRoles} from '../../../interfaces/interfaces';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {RolesService} from '../../../services/roles.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-create-role',
  templateUrl: './create-role.component.html',
  styleUrls: ['./create-role.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class CreateRoleComponent implements OnInit {

  public NewRoleForm: FormGroup;
  errorMessage = '';
  permissionFilter = '';
  loading = false;
  loadingPermissions = false;
  public permissions: string[] = [];
  public rolePermission: string[] = [];

  constructor(private formBuilder: FormBuilder,
              private rolesService: RolesService,
              private router: Router) {
  }

  ngOnInit() {
    this.getPermissions();
    this.buildForm();
  }

  buildForm() {
    this.NewRoleForm = this.formBuilder.group({
      name: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
    })
  }

  onChange(permission: string, isChecked: boolean) {
    const index = this.rolePermission.indexOf(permission);
    if (index > -1) {
      this.rolePermission.splice(index);
    }

    if (isChecked) {
      this.rolePermission.push(permission);
    }
  }

  onSubmit() {
    const v = {
      name: this.NewRoleForm.get('name').value,
      permissions: this.rolePermission
    };
    this.loading = true;
    if (v.name) {
      this.rolesService.insertRole(v).subscribe((role: any) => {
          if (role) {
            Swal.fire('Updated', 'New role created successfully', 'success');
            this.router.navigate(['/app/roles'])
          }
          this.loading = false;
        }, (err) => {
          this.errorMessage = Constants.handleError(err, this.router);
          this.loading = false;
          Swal.fire('Error', this.errorMessage, 'error');
        }
      )
    }
  }

  removeUnderScore(name) {
    while (name.indexOf('_') > -1) {
      name = name.replace('_', ' ');
    }

    return name;
  }

  get filteredPermissions() {
    return this.permissions.filter(permission => permission.toLowerCase().indexOf(this.permissionFilter.toLowerCase()) > -1);
  }

  public getPermissions(): void {
    this.loadingPermissions = true;
    this.rolesService.getPermissions().subscribe((permissions: string[]) => {
        this.permissions = permissions;
        this.loadingPermissions = false;
      }, (err) => {
        Constants.handleError(err, this.router, true);
        this.loadingPermissions = false;
      }
    )
  }

}
