import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';
import {IRoles} from '../../../interfaces/interfaces';
import {RolesService} from '../../../services/roles.service';
import {ActivatedRoute, Router} from '@angular/router';
import {AuthService} from '../../../services/auth.service';
import {AppData} from '../../../data';

@Component({
    selector: 'app-roles-list',
    templateUrl: './roles-list.component.html',
    styleUrls: ['./roles-list.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class RolesListComponent implements OnInit {

    public roles: IRoles[] = [];
    public role: IRoles;
    loading: boolean;
    errorMessage = '';

    constructor(public rolesService: RolesService,
                private route: ActivatedRoute,
                private router: Router) {
    }

    ngOnInit() {
        this.getRoles();
    }

    public getRoles(value = ''): void {
        this.loading = true;
        this.roles = [];
        this.rolesService.getRoles(value).subscribe((roles: IRoles[]) => {
                this.roles = roles;
                this.loading = false;
            }, (err) => {
                this.errorMessage = Constants.handleError(err, this.router);
                this.loading = false;
            }
        );
    }

    deleteRoleSelected() {
    }

    can(permission) {
        return AuthService.can(permission);
    }

    get permissions() {
        return AppData.permissions;
    }

    deleteRole(role, i) {
        Swal.fire({
            title: 'Are you sure?',
            text: 'Do you really want to delete this role?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.rolesService.deleteRole(role.name).subscribe((status: boolean) => {
                    if (status) {
                        Swal.fire('Deleted!', 'Role has been deleted!', 'success');
                        this.roles.splice(i, 1);
                    }
                }, (err) => {
                    Constants.handleError(err, this.router, true);
                });
            }
        })
    }

    checkAll(ev) {
        if (this.roles) {
            this.roles.forEach(x => x.selected = ev.target.checked)
        }
    }

    isAllChecked() {
        if (this.roles) {
            return this.roles.every(_ => _.selected);
        }
        return false;
    }

}
