import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {RouterModule, Routes} from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {RolesListComponent} from './roles-list/roles-list.component';
import { CreateRoleComponent } from './create-role/create-role.component';
import { EditRoleComponent } from './edit-role/edit-role.component';
import {CustomErrorModule} from '../../shared/custom-error/custom-error.module';
import {LoaderModule} from '../../shared/loader/loader.module';

export const routes: Routes = [
  {path: 'create', component: CreateRoleComponent},
  {path: 'edit/:name', component: EditRoleComponent},
  {path: '', redirectTo: 'list', pathMatch: 'full'},
  {path: 'list', component: RolesListComponent}
];


@NgModule({
  declarations: [
      RolesListComponent,
      CreateRoleComponent,
      EditRoleComponent
  ],
  imports: [
    CommonModule,
    PerfectScrollbarModule,
    RouterModule.forChild(routes),
    FormsModule, CustomErrorModule,LoaderModule,
    ReactiveFormsModule
  ]
})
export class RolesModule { }
