import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import Swal from 'sweetalert2';
import {Constants} from '../../../app.constants';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {RolesService} from '../../../services/roles.service';

@Component({
  selector: 'app-edit-role',
  templateUrl: './edit-role.component.html',
  styleUrls: ['./edit-role.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class EditRoleComponent implements OnInit {

  public roleForm: FormGroup;
  permissionFilter = '';
  role: any = false;
  loading = false;
  pageLoading = false;
  errorMessage: string;
  public allPermissions: string[];
  public rolePermission: string[];
  private original_name: string;


  constructor(private router: Router,
              private route: ActivatedRoute,
              private rolesService: RolesService,
              private formBuilder: FormBuilder) {
  }

  ngOnInit() {
    this.buildForm();
    const name = this.route.snapshot.params['name'];
    this.original_name = name;
    this.getRolesDetail(name);
  }

  removeUnderScore(name) {
    while (name.indexOf('_') > -1) {
      name = name.replace('_', ' ');
    }

    return name;
  }

  get filteredPermissionList() {
    return this.allPermissions.filter(permission => permission.toLowerCase().indexOf(this.permissionFilter.toLowerCase()) > -1);
  }

  getRolesDetail(name: string) {
    this.pageLoading = true;
    this.rolesService.getRole(name).subscribe((result) => {
        this.role = result.role;
        this.rolePermission = result.permissions;
        this.allPermissions = result.all_permissions;
        this.pageLoading = false;

        this.roleForm.patchValue({
          name: this.role.name
        });
      }, (err) => {
        this.pageLoading = false;
        this.errorMessage = Constants.handleError(err, this.router);
      }
    );
  }

  buildForm() {
    this.roleForm = this.formBuilder.group({
      name: ['', Validators.required],
    });
  }

  onChange(permission: string, isChecked: boolean) {
    const index = this.rolePermission.indexOf(permission);
    if (index > -1) {
      this.rolePermission.splice(index);
    }

    if (isChecked) {
      this.rolePermission.push(permission);
    }
  }

  onSubmit() {
    const v = {
      name: this.roleForm.get('name').value,
      permissions: this.rolePermission
    };
    this.loading = true;
    if (v.name) {
      this.rolesService.updateRole(this.original_name, v).subscribe((role: any) => {
          if (role) {
            Swal.fire('Updated', 'Role\'s record successfully updated', 'success');
            this.router.navigate(['/app/roles'])
          }
          this.loading = false;
        }, (err) => {
          Constants.handleError(err, this.router, true);
          this.loading = false;
        }
      )
    }
  }

}
